* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  color: #1a202c;
  line-height: 1.6;
  min-height: 100vh;
}

code {
  font-family: "SF Mono", "Monaco", "Cascadia Code", "Roboto Mono", Consolas,
    "Courier New", monospace;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 123, 191, 0.1),
    0 4px 12px rgba(0, 123, 191, 0.05), 0 1px 3px rgba(0, 0, 0, 0.05);
  padding: 32px;
  margin-bottom: 24px;
  border: 1px solid rgba(0, 123, 191, 0.08);
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 20px 60px rgba(0, 123, 191, 0.15),
    0 8px 24px rgba(0, 123, 191, 0.08), 0 2px 6px rgba(0, 0, 0, 0.08);
  transform: translateY(-2px);
}

.btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  border: none;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  letter-spacing: 0.025em;
  position: relative;
  overflow: hidden;
}

.btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transition: left 0.5s ease;
}

.btn:hover:before {
  left: 100%;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #007bbf 0%, #005a8b 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(0, 123, 191, 0.3);
}

.btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #0056a3 0%, #003d5c 100%);
  box-shadow: 0 6px 20px rgba(0, 123, 191, 0.4);
  transform: translateY(-2px);
}

.btn-secondary {
  background: linear-gradient(135deg, #e2e8f0 0%, #cbd5e1 100%);
  color: #475569;
  box-shadow: 0 2px 8px rgba(71, 85, 105, 0.15);
}

.btn-secondary:hover:not(:disabled) {
  background: linear-gradient(135deg, #cbd5e1 0%, #94a3b8 100%);
  box-shadow: 0 4px 12px rgba(71, 85, 105, 0.2);
  transform: translateY(-1px);
}

.btn-danger {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(220, 38, 38, 0.3);
}

.btn-danger:hover:not(:disabled) {
  background: linear-gradient(135deg, #b91c1c 0%, #991b1b 100%);
  box-shadow: 0 6px 20px rgba(220, 38, 38, 0.4);
  transform: translateY(-2px);
}

.form-group {
  margin-bottom: 24px;
}

.form-label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #2d3748;
  font-size: 14px;
  letter-spacing: 0.025em;
}

.form-input,
.form-select,
.form-textarea {
  width: 100%;
  padding: 14px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 14px;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(5px);
}

.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  outline: none;
  border-color: #007bbf;
  box-shadow: 0 0 0 4px rgba(0, 123, 191, 0.1),
    0 4px 12px rgba(0, 123, 191, 0.15);
  background: rgba(255, 255, 255, 0.95);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.alert {
  padding: 16px 20px;
  border-radius: 12px;
  margin-bottom: 20px;
  border-left: 4px solid;
  backdrop-filter: blur(10px);
}

.alert-info {
  background: rgba(0, 123, 191, 0.08);
  color: #1e40af;
  border-left-color: #007bbf;
}

.alert-success {
  background: rgba(16, 185, 129, 0.08);
  color: #059669;
  border-left-color: #10b981;
}

.alert-error {
  background: rgba(220, 38, 38, 0.08);
  color: #dc2626;
  border-left-color: #dc2626;
}

.grid {
  display: grid;
  gap: 32px;
}

.grid-2 {
  grid-template-columns: 1fr 1fr;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.gap-4 {
  gap: 16px;
}

.text-lg {
  font-size: 18px;
}

.text-xl {
  font-size: 20px;
  font-weight: 600;
}

.text-2xl {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: -0.025em;
}

.font-bold {
  font-weight: 700;
}

.font-semibold {
  font-weight: 600;
}

.text-gray-600 {
  color: #718096;
}

.text-blue-600 {
  color: #007bbf;
}

.text-red-500 {
  color: #dc2626;
}

.mb-4 {
  margin-bottom: 16px;
}

.mb-6 {
  margin-bottom: 24px;
}

.mt-4 {
  margin-top: 16px;
}

.mt-6 {
  margin-top: 24px;
}

@media (max-width: 768px) {
  .grid-2 {
    grid-template-columns: 1fr;
  }

  .container {
    padding: 0 16px;
  }

  .card {
    padding: 20px;
    margin-bottom: 16px;
  }

  .text-2xl {
    font-size: 24px;
  }
}
